import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

const Disclaimer = () => {
    return (
        <Layout>
            <Helmet>
                <title>Friskrivning | Orpose.se</title>
                <meta name="description" content={"Hitta rätt produkt för just dina krav och preferenser på Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/legal/disclaimer"} />
            </Helmet>
            <Document>
                <h3>Friskrivning</h3>
                <p>Vårt mål på Orpose Group AB är att presentera produkter som passar våra användare så bra som möjligt. Därför strävar vi efter att all information ska vara korrekt samt att vi ska uppdatera ny information så snabbt som möjligt. Vi garanterar dock inte sanningen och korrektheten i den information som står på produktsidan.</p>
                <br/>
                <p>Vi garanterar inte heller det som sedan sker, presenteras eller avtalas om på sidor som vi skickar dig till, detta är oberoende tredjeparter. Ställning och relation mellan dig och tredjeparter är endast mellan er. Vi ansvarar därav inte för något som sker där, exempelvis köp, betalningar eller frakt av produkt. Därför uppmanar vi dig att alltid granska informationen och att hemsidan du dirigeras till stämmer samt är trovärdig.</p>
                <br/>
                <p>Algoritmen som räknar ut vad den perfekta matchen för dig är kan endast gå på den information som har blivit gett till den och kan sedan endast para ihop denna profil med de produkter som finns på vår databas. Detta betyder att underlaget för att hitta den bästa produkten kan vara tunt och därav kan vi inte heller garantera att den produkten vi visar för dig verkligen är den bästa och/eller att du är ense med det vår algoritm föreslår.</p>
            </Document>
        </Layout>
    );
};

export default Disclaimer;
