import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

const Cookies = () => {
    return (
        <Layout>
            <Helmet>
                <title>Cookies | Orpose.se</title>
                <meta name="description" content={"Hitta rätt produkt för just dina krav och preferenser på Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/legal/cookies"} />
            </Helmet>
            <Document>
                <h3>Cookies</h3>
                
                <h4>Om lagring av data och kakor på Orpose</h4>
                <p>För att webbsidan Orpose.com ska bli så bra som möjligt används lokal lagring av data, såsom kakor (eller cookies som det ibland kallas för). Enligt Lag (2003:389) om elektronisk kommunikation behöver vi (Orpose Group AB) lämna ut följande information för att du ska kunna samtycka till användandet av kakor eller inte. </p>
                
                <h4>Vilka kakor används?</h4>
                <p>På vår webbsida används följande kakor:</p>
                <ul>
                    <li>Session cookies</li>
                    <li>Persistent eller ihållande cookies</li>
                </ul>

                <h4>Ändamål</h4>
                <p>Kakor som samlas in används av oss och säljs inte vidare till tredjeparter eller ges ut på något annat sätt*. På vår sida används de för följande: </p>

                <p>Lokal lagring av data används på Orpose för att samla in statistik som är nödvändigt för att utveckla vår sida till det bättre. Då blir också upplevelsen för dig som användare bättre. Den lokala lagringen används även för att vi ska kunna genomföra A/B-tester för att testa nya funktioner på en mindre grupp användare innan vi släpper dem till allmänheten.</p>

                <h4>Hur länge lagras de?</h4>
                <p>Kakorna sparas i upp till två år efter det att du senast besökte sidan.</p>

                <h4>Vilken information samlas in?</h4>
                <p>Informationen som samlas in är följande:</p>
                <ul>
                    <li>Vilka sidor du besöker.</li>
                    <li>Hur länge du stannar på sidan.</li>
                    <li>Från vilket land du besöker webbsidan.</li>
                </ul>

                <h4>Så hanterar du användandet av kakor</h4>
                <p>Du kan neka till att använda kakor, du kan granska vilka kakor som används samt du kan, genom funktioner på din webbläsare (webbläsarens hjälpsidor), radera nuvarande lokal lagring av data eller bli informerad varje gång en webbplats begär att få lagra kakor. </p>

                <h4>Konsekvenser vid nekande</h4>
                <p>Konsekvenser vid nekande för användning av kakor är bland annat att vi får sämre underlag för att kunna utveckla och förbättra vår sida vilket kan leda till en försämrad upplevelse på Orpose.</p>

                <h4>Övrigt</h4>
                <p>Vill du ha mer information från oss är du välkommen att kontakta oss via e-postadress.</p>
                <br/>
                <p><i>*Dock använder domänen och servern egna kakor som gör användarna anonyma, detta för att sidan ska fungera samt vara säker.</i></p>
            </Document>
        </Layout>
    );
}

export default Cookies;
