import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

const Career = () => {
    return (
        <Layout>
            <Helmet>
                <title>Karriärer | Orpose.se</title>
                <meta name="description" content={"Hitta rätt produkt för just dina krav och preferenser på Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/career"} />
            </Helmet>
            <Document>
                <h3>Karriärer</h3>
                
                <h4>Jobba hos oss</h4>
                <p>Orpose Group AB är ett start-up företag baserat i Jönköping med målet att <em>hjälpa</em> och <em>vägleda</em> människor hitta rätt produkter efter deras <em>krav</em> och <em>preferenser</em>.</p>
                <br />
                <p>Är du intresserad eller nyfiken över att gå med i vårt team och expandera <em>möjligheterna</em> inom <em>internetköp</em> med oss?</p>
                <br />
                <p>Tveka inte på att höra av dig! </p>
                <a href="mailto:contact@orpose.com">contact@orpose.com</a>
                
                <h4>Vi är i nöd av</h4>
                <p>Orpose Group behöver främst python-programmerare med kunskaper inom <em>machine learning</em> och <em>deep learning</em> för att b.la utveckla matchningssystemet.</p>

                <h4>Detta behöver vi också</h4>
                <p>Orpose Group behöver programmerare som kan något eller några av följande språk:</p>
                <ul>
                    <li><em>Python</em> (helst bekant med Django)</li>
                    <li><em>JavaScript</em> (helst bekant med React.js)</li>
                    <li><em>Html/Css</em></li>
                </ul>
            </Document>
        </Layout>
    );
};

export default Career;
