import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Customization from "./Customization";

/**
 * The laptop category customizer.
 */
const LaptopCategory = props => {
    // The customizers for the category.
    let customizers = [
        {type: "usage", title: "Välj användningsområde", name: "usage", expanded: true, options: [
            {name: "general", title: "Allmänt", description: "Surfande och Office-program"},
            {name: "gaming", title: "Gaming", description: "Spel och andra resurskrävande program"},
        ]},
        {type: "range", title: "Välj budget (kr)", name: "price", expanded: true, ranges: {
            general: {min: 0, max: 24000, step: 1000},
            gaming: {min: 4000, max: 35000, step: 1000},
        }, description: "Priset på laptopen"},
        {type: "priority", title: "Välj dina prioriteringar", name: "priorities", expanded: true, options: [
            {name: "performance", title: "Prestanda", description: "Hög prestanda"},
            {name: "storage", title: "Lagring", description: "Lagra stora filer"},
            {name: "screen", title: "Skärm", description: "Kvaliteten på skärmen"},
            {name: "weight", title: "Vikt", description: "Lätt att bära"},
            {name: "battery", title: "Batteritid", description: "Lång batteritid"},
        ]},
        {type: "range", title: "Välj skärmstorlek (tum)", name: "size", expanded: false, ranges: {
            general: {min: 10, max: 17, step: 1},
            gaming: {min: 10, max: 17, step: 1},
        }, description: "Storleken på skärmen"},
    ];
    
    let steps = ["usage"];
    if (window.google_optimize.get("Kh3bfh9oQWyAOB_XEw71yQ") === "1")
        steps = ["usage", "price", "priorities"];
    
    return (
        <Layout>
            <Helmet>
                <title>Hitta rätt laptop för just dig | Orpose.se</title>
                <meta name="description" content="Verktyget som hjälper dig att hitta rätt laptop för just dina krav och preferenser. Hitta även produkter såsom laptops, ..." />
                <link rel="canonical" href="https://www.orpose.se/category/laptop" />
            </Helmet>
            <Customization customizers={customizers} steps={steps} />
        </Layout>
    );
};

export default LaptopCategory;
