import React from "react";
import Helmet from "react-helmet";
import Layout from "../../components/Layout";
import Document from "../../components/Document";

import "./About.css";

const About = () => {
    return (
        <Layout>
            <Helmet>
                <title>Om oss | Orpose.se</title>
                <meta name="description" content={"Hitta rätt produkt för just dina krav och preferenser på Orpose"} />
                <link rel="canonical" href={"https://www.orpose.se/about"} />
            </Helmet>
            <Document>
                <p className="large">Vårt mål är att lösa konsumenters bekymmer och behov</p>
                <p>Orpose Group AB grundades i slutet av december 2019 i Jönköping, vars tjänst Orpose lanserades för allmänheten två och en halv månader senare. Orpose, är en tjänst som är till för att hjälpa människor hitta den bästa produkten för just dem. Idén bakom Orpose kom ifrån svårheten med att hitta information om teknikprodukter samt tröttheten med att alla jämförelsesidor endast jämför priset. Billigast är inte alltid bäst. Rätt produkt för dig med bäst kvalité och prestanda till lägst pris är bäst. Att välja mellan flera alternativ kan vara väldigt svårt och inte minst när man inte är specialiserad inom ett ämne. Därför lanserades Orpose, som en objektiv produktmatchningssida med visionen om att göra internetköp av teknikprodukter lätt för hela allmänheten.</p>
            </Document>
        </Layout>
    );
};

export default About;
